<template>
  <div class="certifications">
    <p class="introduction">
      Feel free to check out my profiles on <a :href=social.acclaim target="_blank">Acclaim (Youracclaim)</a>
      and <a :href=social.accredible target="_blank">Accreddible (Credential.net)</a> for a list of all exams and certifications.
      Most of my certifications and exams are within Microsoft and Zendesk systems and products.
    </p>
    <sui-card-group :items-per-row="2">
      <sui-card v-for="certification in certifications" v-bind:key="certification.name"
      class="link" @click='window.open(certification.certification_url, "_blank")'>
        <sui-card-content>
          <sui-card-header>{{certification.name}}</sui-card-header>
          <sui-card-meta>
            <span class="category">{{certification.category}}
            </span>
          </sui-card-meta>
          <sui-card-description>
            {{certification.description}}
          </sui-card-description>
        </sui-card-content>
        <sui-card-content extra>
          <span>Achieved: {{certification.achieved}}</span>
          <span slot="right">
            {{certifications.thumbnail}}
            <sui-image :src='getCertificationThumbnailSrc(certification.thumbnail)' shape="circular" size="mini" />
          </span>
        </sui-card-content>
      </sui-card>
    </sui-card-group>
  </div>
</template>
<script>
import Certifications from '@/config/certifications.json'
import Social from '@/config/social.json'

export default {
  name: 'Certifications',
  data: () => {
    return {
      certifications: Certifications,
      social: Social,
      window: window
    }
  },
  methods: {
    getCertificationThumbnailSrc (src) {
      return require('../assets/certifications/img/' + src)
    }
  }
}
</script>
<style>
.certifications {
  max-width: 600px;
  margin: 0 auto;
}
</style>
